import React, { memo, useEffect, useState } from "react";
import { Modal } from "react-bootstrap";
import { Link } from "react-router-dom";
import styled from "styled-components";

const EventModal = ({ className }) => {
  const [show, setShow] = useState(false);

  useEffect(() => {
    const imgaes = new Image();
    imgaes.src = "https://www.w3schools.com/w3css/img_lights.jpg";
    imgaes.onload = () => {
      setShow(true);
    };
    return () => {};
  }, []);
  return (
    <Modal
      show={show}
      className={className}
      contentClassName="modal__custom p-0"
      dialogClassName="modal__custom__dialog "
      centered
    >
      <div className="modal-header modal__custom__header">
        <button
          type="button"
          className="btn-close modal__custom__button--close text-white"
          data-bs-dismiss="modal"
          aria-label="Close"
          onClick={() => setShow?.(false)}
          style={{ filter: "invert(1)" }}
        ></button>
      </div>
      <div className="modal-body modal__custom__body p-0">
        <img
          src="https://www.w3schools.com/w3css/img_lights.jpg"
          alt=""
          className="mb-3"
          onLoad={() => setShow(true)}
        />
        {/* <p className="fs__title">Báo cáo của bạn đang được xuất</p> */}
        {/* <p className="fs__title mt-3">VIETCOMBANK</p>
        <p className="d-flex justify-content-between">
          Số tài khoản: <span>19030001119333</span>
        </p>
        <p className="d-flex justify-content-between">
          Số điện thoại: <span>0123456789</span>
        </p>
        <p className="d-flex justify-content-between">
          Tên đầy đủ: <span>Nguyễn Văn A</span>
        </p>
        <p className="d-flex justify-content-between">
          Số tiền: <span>499.000 VND</span>
        </p> */}
        <div className="row px-3">
          <div className="col-12 col-lg-3 align-items-start d-flex">
            <span className="fs__title fw-bold">Get your point Across</span>
          </div>
          <div className="col-12 col-lg-9">
            <p
              className="fw-bolder fs__text"
              style={{
                borderColor: "#ffc367 !important",
                borderWidth: "2px !important",
              }}
            >
              Lưu ý:
            </p>
            <p className="fs__text">
              - Số lượt xuất pdf của bạn sẽ bị giảm khi nhấn vào nút "Xuất pdf"
            </p>
            <p className="fs__text">
              - Bạn có thể tăng lhoặc{" "}
              <Link to="/agency-policy">
                - Bạn có thể tăng lượt xuất pdf bằng việc nhập mã hoặc{" "}
                <Link to="/agency-policy">trở thành đại lý</Link>
              </Link>
            </p>
          </div>
        </div>
      </div>
      <div className="modal-footer modal__custom__footer">
        <button
          type="button"
          className="btn button-secondary--outline"
          data-bs-dismiss="modal"
          onClick={() => setShow?.(false)}
        >
          Show me how
        </button>
        <button type="button" className="btn button-secondary" onClick={() => setShow?.(false)}>
          Learn More
        </button>
      </div>
    </Modal>
    // <div
    //   className={`${className} modal show`}
    //   id="staticBackdrop"
    //   data-bs-backdrop="static"
    //   data-bs-keyboard="false"
    //   tabIndex="-1"
    //   aria-labelledby="staticBackdropLabel"
    //   aria-hidden="true"
    // >

    // {/* </div> */}
  );
};

export default memo(styled(EventModal)`
  .modal__custom {
    position: relative;
    background-color: #fafafa;
    color: black;
    &__dialog {
      max-width: fit-content;
    }
    &__body {
      img {
        border: none;
        width: 100%;
        max-height: 350px;
        object-fit: cover;
      }
    }
    &__header {
      border: none;
      position: absolute;
      right: 8px;
      top: 8px;
      padding: 0;
    }
    &__footer {
      border: 0;
      button {
        border-radius: 0;
      }
    }
    &__button--close {
      z-index: 2;
      margin: 0;
      color: #fafafa !important;
      font-weight: bolder;
      &:hover {
        color: ${(props) => props.theme.colorTextTitle};
      }
    }
  }
`);
